@use 'variables' as v;

a{
    font-size: 1.1rem;
}
h1{
    font-size: 3.85em;
    line-height: 1.2em;
}
h2, .h2{
    font-size: 2.85em;
    line-height: 1.2em;
    font-family: v.$bold;
    &.up{
        font-size: 3em;
    }
    &.down{
        font-size: 2.15em;
        font-family: v.$medium;
        font-weight: 100;
    }
}
h3, .h3{
    font-size: 1.65em;
    line-height: 1.2em;
}

p, .text{
    font-size: 1.1rem;
}